<template>
    <div>
        <div class="container">
       <div class="main__items" v-if="posts">
        <div class="main__items-item" v-for="post in posts" :key="post.id">
            <router-link :to="/Post/ + post.id" ><a href="">{{ post.title }}</a></router-link>
            <p>Body: {{ post.body }}</p>
        </div>
       </div>
       <div v-else>
            Идет загрузка...
       </div>
    </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getPosts()
        },
        computed: {
            posts() {
                return this.$store.state.posts
            }
        },
        methods: {
            getPosts() {
                this.$store.dispatch('getPosts')
            }
        }
    }
</script>

